header{
    color:white;
    width: 100%;
    text-align: center;
    position: relative;
    /* background-color: rgb(36, 100, 238); */
    background-color: rgb(26, 75, 182);
    border-radius: 3px;
    box-shadow: 0 2rem 3rem 1rem rgba(0, 0, 0, 0.15);
    border: 3px solid rgb(16, 66, 173);
}

h2{
    font-size: 2.3rem;
    margin-top: 0px;
}

a{
    position: absolute;
    bottom: 3px;
    right: 3px;
    text-decoration: none;
    color: white;
}
a:hover{
    text-decoration: underline;
}
